"use client";

import { Label } from "@/components/ui/label";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";

export default function EditListing() {
  return (
    <div className="px-10 md:px-36 my-10">
      <h2 className="font-bold text-2xl mb-6">
        Enter some more details about your listing
      </h2>

      <div className="p-8 rounded-lg shadow-md bg-white">
        {/* FIRST ROW */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
          {/* RENT / SELL */}
          <div className="flex flex-col gap-2">
            <Label className="text-slate-500">Do you want to Rent or Sell?</Label>
            <RadioGroup defaultValue="Sell">
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="Sell" id="Sell" />
                <Label htmlFor="Sell">Sell</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="Rent" id="Rent" />
                <Label htmlFor="Rent">Rent</Label>
              </div>
            </RadioGroup>
          </div>

          {/* PROPERTY TYPE */}
          <div className="flex flex-col gap-2">
            <Label className="text-slate-500">Property Type</Label>
            <Select>
              <SelectTrigger>
                <SelectValue placeholder="Select Property Type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="Single Family House">
                  Single Family House
                </SelectItem>
                <SelectItem value="Town House">Town House</SelectItem>
                <SelectItem value="Condo">Condo</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </div>

        {/* SECOND ROW */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
          <div>
            <Label>Bedroom</Label>
            <Input placeholder="Ex. 2" />
          </div>

          <div>
            <Label>Bathroom</Label>
            <Input placeholder="Ex. 2" />
          </div>

          <div>
            <Label>Built In</Label>
            <Input placeholder="Ex. 1900 Sq.ft" />
          </div>
        </div>

        {/* THIRD ROW */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
          <div>
            <Label>Parking</Label>
            <Input placeholder="Ex. 2" />
          </div>

          <div>
            <Label>Lot Size (Sq.Ft)</Label>
            <Input />
          </div>

          <div>
            <Label>Area (Sq.Ft)</Label>
            <Input placeholder="Ex. 1900" />
          </div>
        </div>

        {/* FOURTH ROW */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
          <div>
            <Label>Selling Price ($)</Label>
            <Input placeholder="400000" />
          </div>

          <div>
            <Label>HOA (Per Month) ($)</Label>
            <Input placeholder="100" />
          </div>
        </div>

        {/* DESCRIPTION */}
        <div className="flex flex-col gap-2">
          <Label>Description</Label>
          <Textarea placeholder="Describe your property..." />
        </div>
      </div>
    </div>
  );
}
